$parentClassName: featured-work;

.#{$parentClassName} {
	width: 100%;
	height: 100px;

	display: flex;
	justify-content: center;
	position: relative;

	background: {
		position: center;
		repeat: no-repeat;
		size: 100% auto;
		color: rgba(128, 128, 128, 0.426);
	}

	border: 2.5px solid;
	border-bottom: none;

	* {
		background: transparent;
	}

	.#{$parentClassName}__container {
		z-index: 2;
		width: 90%;

		padding: {
			top: 15px;
			bottom: 15px;
		}
		position: relative;
		cursor: pointer;

		.#{$parentClassName}__inner {
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;
			color: white;

			.#{$parentClassName}__title,
			.#{$parentClassName}__data {
				cursor: auto;
				transition: all 1s ease 0s;
				-webkit-transition: all 1s ease 0s;
				-moz-transition: all 1s ease 0s;
				-ms-transition: all 1s ease 0s;
				-o-transition: all 1s ease 0s;
			}
			.#{$parentClassName}__title {
				font: {
					family: "Inter", sans-serif;
					size: 28px;
					weight: 600;
				}
				letter-spacing: 1px;
				text: {
					transform: uppercase;
				}
			}
			.#{$parentClassName}__data {
				display: flex;
				align-items: center;
				font-size: 14px;
			}
		}
	}

	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		backdrop-filter: grayscale(1) blur(5px);
		background: transparent;
		transition: all 1s ease 0s;
		-webkit-transition: all 1s ease 0s;
		-moz-transition: all 1s ease 0s;
		-ms-transition: all 1s ease 0s;
		-o-transition: all 1s ease 0s;
	}
}

.#{$parentClassName}:last-child {
	border-bottom: 2.5px solid white;
}

.#{$parentClassName}:nth-child(even) {
	.#{$parentClassName}__container .#{$parentClassName}__inner {
		flex-direction: row-reverse;
	}
}

.#{$parentClassName}:hover {
	&::before {
		backdrop-filter: grayscale(0) blur(5px);
	}
	.#{$parentClassName}__title,
	.#{$parentClassName}__data {
		color: gray;
	}
}
