@use "/src/styles/colors/global.module" as colors;
@use "/src/styles/fonts/global.module" as fonts;

$main-info-color: map-get(colors.$types, black-var, default);

$parentClassName: main-info;

.#{$parentClassName} {
	color: $main-info-color;
	position: relative;
	text-transform: uppercase;
	white-space: break-spaces;
	font-weight: 600;
	.#{$parentClassName}__inner {
		.#{$parentClassName}__list {
			position: relative;
			.#{$parentClassName}__list-element {
				display: inline;
				margin-right: 20px;

				/*
				&::after {
					content: "";
					width: 5px;
					height: 5px;
					top: 0;
					right: 0;
					background: white;
					position: absolute;
					border-radius: 100%;
					-webkit-border-radius: 100%;
					-moz-border-radius: 100%;
					-ms-border-radius: 100%;
					-o-border-radius: 100%;
				}
				*/
			}
			font: {
				family: "Inter";
			}
			line-height: 20px;
			pointer-events: auto;
			position: relative;
		}
	}
}
