@use "/src/styles/fonts/global.module" as fonts;
@use "/src/styles/colors/global.module" as colors;

$parentClassName: featured-works;

.#{$parentClassName} {
	width: 100%;
	display: flex;
	justify-content: center;
	border: {
		bottom: 2.5px solid white;
	}
	.#{$parentClassName}__container {
		margin: 75px 0;
		width: 80%;
		.#{$parentClassName}__inner {
			width: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			.#{$parentClassName}__description {
				background: rgba(194, 194, 194, 0.654);
				display: flex;
				justify-content: space-between;
				height: 75px;
				width: 100%;
				div,
				h1 {
					display: flex;
				}
			}
			.#{$parentClassName}__list {
				width: 100%;
				display: flex;
				flex-direction: column;
				margin: 50px 0;
				position: relative;
			}
			.#{$parentClassName}__endMsg {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-bottom: 50px;
				font: {
					family: "Inter";
				}
				letter: {
					spacing: 0.25px;
				}
			}
			.#{$parentClassName}__buttons {
				display: flex;
				justify-content: center;
				width: 100%;
				.#{$parentClassName}__button:nth-child(odd) {
					margin-right: 25px;
				}
				.#{$parentClassName}__button:nth-child(even) {
					margin-left: 25px;
				}
			}
		}
	}
}
